var elem = ["shorts.png", "dress.png", "cat.png"];




function getImg() {
    var elemSize=elem.length;
    var randIndex = 0;
    if (elemSize == 1)

    {
    return elem[0];
    }

    randIndex = Math.floor(Math.random() * (elemSize));
    return elem[randIndex]
}

function addmg() {
    var elem = document.getElementById('sx');
    elem.setAttribute('src', 'imgs/'+ getImg())
}

